<template>
  <div class="login">
    <!-- el-from 是表单组件
      :label-option 设置label对齐方式 left
      Label-width 设置label的宽度 -->
    <!-- model 表单绑定的数据 -->

    <el-form
      :label-position="labelPosition"
      :label-width="labelWidth"
      :model="loginFrom"
      ref="userForm"
    >
      <!-- 
        el-from-item 需要的是el-from 的子组件
        label: 填写label信息
        label-width 设置当前 el-from-item 的 label的宽度
       -->
      <el-form-item :rules="usernameRules" prop="username" label="用户名">
        <el-input v-model="loginFrom.username"></el-input>
      </el-form-item>

      <el-form-item :rules="passwordRules" prop="password" label="密码：">
        <el-input v-model="loginFrom.password"></el-input>
      </el-form-item>
      <el-button @click="onSubmit('userForm')" size="medium" type="primary"
        >登录</el-button
      >
    </el-form>
  </div>
</template>

<script>
import { register } from "../../api/register.js";
export default {
  name: "Register",
  data() {
    /**
     * validateUserName 自定义验证函数
     * rule
     * value 表单验证数据
     * callback 回调函数
     *      回参提示信息
     */
    const validateUserName = (rule, value, callback) => {
      if (!value) {
        return callback(new Error("用户名不能为空"));
      }
      if (value.length < 4) {
        return callback(new Error("用户名不能小于4"));
      }
      if (value.length > 6) {
        return callback(new Error("用户名不能大于6"));
      }
      let pattern = /\w{4,6}/;
      if (!pattern.test(value)) {
        return callback(new Error("请输入4-6位数字字母下划线"));
      }
      callback();
    };
    const validatePassWord = (rule, value, callback) => {
      if (!value) {
        return callback(new Error("密码不能为空"));
      }
      if (value.length < 6) {
        return callback(new Error("密码不能小于4"));
      }
      if (value.length > 16) {
        return callback(new Error("密码不能大于6"));
      }
      let pattern = /\w{6,16}/;
      console.log(pattern.test(value));
      if (!pattern.test(value)) {
        alert(1);
        return callback(new Error("请输入4-6位数字字母下划线"));
      }
      callback();
    };
    return {
      userForm: "userForm",
      loginFrom: {
        username: "",
        password: "",
      },
      labelPosition: "right",
      labelWidth: "80px",
      usernameRules: {
        validator: validateUserName,
        trigger: "blur",
      },
      passwordRules: [
        {
          validator: validatePassWord,
          trigger: "blur",
        },
      ],
    };
  },
  mounted() {},
  methods: {
    onSubmit(userForm) {
      console.log(userForm);
      /**
       * this.$refs[ref] 获取绑定元素的dom元素
       * Form Method
       */
      this.$refs[userForm].validate((valid) => {
        console.log(valid);
        this.setRegister();
      });
    },
    setRegister() {
      return register(this.loginFrom).then((data) => {
        if (data.code == 1) {
          this.$message({
            message: "注册成功",
            type: "success",
          });

          this.$router.push("/login");
        }
      });
    },
  },
};
</script>

<style lang="less">
.login {
  width: 400px;
  margin: 80px auto;
  box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.3);
  padding: 30px;
}
</style>